<template>
  <div class="row bg-platinum">
    <div class="col-md-12 bg-platinum p-0">
      <h1 class="text-center bg-darkcyan text-platinum py-3 border-bottom" id="blog">Blog</h1>
    </div>
    <div class="col-md-12 text-center bg-platinum my-2" v-for="(item, index) in posts" :key="index">
      <div
        class="col-md-12 p-3 p-md-5 text-platinum rounded bg-platinum d-flex justify-content-center blogPost"
        :style="{background:`url(${item.cover_image})`}"
      >
        <div style="background: black; opacity: 0.9" class="p-3 rounded">
          <h1 class="display-4 font-italic" style="opacity: 1.0">{{item.title}}</h1>
          <p class="lead my-1 small">{{item.readable_publish_date}}</p>
          <p class="lead my-2">{{item.tag_list.map(tag => '#' + tag).join(', ')}}</p>
          <p class="lead mb-0" @click="modalToggle(item.canonical_url)">
            <i class="fas fa-book-open"></i> Read
          </p>
        </div>
      </div>
    </div>
    <transition name="modal" v-if="showModal">
      <div class="modal-mask bg-darkcyan">
        <div class="modal-wrapper">
          <div class="modal-container bg-platinum">
            <div class="modal-body">
              <iframe :src="modal"></iframe>
            </div>
            <div class="d-flex justify-content-center">
              <button class="btn btn-darkcyan" @click="modalToggle()">OK</button>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      posts: [],
      modal: "",
      showModal: false
    };
  },
  methods: {
    fetchPosts() {
      fetch("https://dev.to/api/articles?username=mrrwmix")
        .then(response => response.json())
        .then(data => (this.posts = data))
        .catch(error => console.error(error));
    },
    modalToggle(url) {
      if (url) {
        this.modal = url;
        this.showModal = true;
      } else {
        this.modal = "";
        this.showModal = false;
      }
    }
  },
  mounted() {
    this.fetchPosts();
  }
};
</script>

<style scoped>
i {
  cursor: pointer;
}

.blogPost {
  background-repeat: no-repeat !important;
  background-position: 50% !important;
}

.lead.mb-0 {
  cursor: pointer;
}

.lead.mb-0:hover {
  color: teal;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 95%;
  height: 95vh;
  margin: 0px auto;
  padding: 0;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 0;
  width: 100%;
  height: 90%;
}

iframe {
  width: 100%;
  height: 100%;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>