<template>
  <div class="row">
    <div class="col-md-12 pb-2 p-0 bg-platinum">
      <h1
        id="portfolio"
        class="text-center bg-darkcyan text-platinum py-3 border-bottom"
      >
        Portfolio
      </h1>
    </div>
    <div class="col-md-12 d-flex justify-content-center flex-wrap bg-platinum">
      <div
        class="card float-left border border-darkcyan m-2 shadow"
        v-for="(item, index) in githubArray"
        :key="index"
        style="width:18rem"
        v-scroll-reveal.reset
      >
        <img
          class="card-img-top"
          alt="card image cap"
          :src="item.image"
          v-if="item.image"
        />

        <div class="card-body">
          <h5 class="card-title text-darkteal">{{ item.name }}</h5>
          <p class="card-text">{{ item.description }}</p>
        </div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item">
            {{ item.language }}
            <span v-if="item.language == null">JavaScript&nbsp;</span>
            <i
              class="fab fa-react text-info"
              v-if="
                item.language == null ||
                  item.description.toLowerCase().includes('react')
              "
            />
            <i
              class="fab fa-vuejs text-darkcyan"
              v-else-if="
                item.language.toLowerCase() === 'vue' ||
                  item.description.toLowerCase().includes('vue')
              "
            ></i>
            <i
              class="fab fa-node text-success"
              v-else-if="item.description.toLowerCase().includes('node')"
            ></i>
            <i
              class="fab fa-js-square text-warning"
              v-else-if="item.language.toLowerCase() === 'javascript'"
            />
            <i
              class="fab fa-java"
              v-else-if="item.language.toLowerCase() === 'java'"
            />
            <i
              class="fab fa-css3-alt text-primary"
              v-else-if="item.language.toLowerCase() === 'css'"
            ></i>
            <i class="fab fa-html5 text-danger" v-else />
          </li>
        </ul>
        <div class="card-body">
          <a
            :href="item.homepage"
            v-if="item.homepage != null && item.homepage != ''"
            class="card-link text-info"
            target="_blank"
            >Deployed URL</a
          >
          <a :href="item.html_url" class="card-link text-info" target="_blank"
            >Source Code</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      githubArray: []
    };
  },
  methods: {
    mouseOver(e) {
      console.log(e.target);
    }
  },
  mounted() {
    axios
      .get(
        'https://api.github.com/users/Mrrwmix/repos?sort=created&per_page=100'
      )
      .then(resp => {
        this.githubArray = resp.data
          .filter(
            item =>
              item.name != 'mrrwmix.github.io' &&
              item.name != 'vue_and_node' &&
              item.name != 'VueBasics' &&
              item.name != 'react_tetris' &&
              item.name != 'HW-Wireframe' &&
              item.name != 'it-logger' &&
              item.name != 'markdown_previewer' &&
              item.name != 'madlibs' &&
              item.name != 'burger' &&
              item.name != 'unit-4-RPG-game' &&
              item.name != 'unit-4-game' &&
              item.name != 'Psychic-Game'
          )
          .map(link => {
            switch (link.name) {
              case 'CodeTimebank':
                return (link = { ...link, image: './images/codetimebank.png' });
              case 'who_pays_the_bill':
                return { ...link, image: './images/whopays.png' };
              case 'book_search':
                return { ...link, image: './images/booksearch.png' };
              case 'clickygame':
                return { ...link, image: './images/clicky.png' };
              case 'WebProwler':
                return { ...link, image: './images/webprowler.png' };
              case 'trip_planner':
                return { ...link, image: './images/trip.png' };
              case 'Opinionated':
                return { ...link, image: './images/opinionated.png' };
              case 'FriendFinder':
                return { ...link, image: './images/friendfinder.png' };
              case 'BuddyGuyBot':
                return { ...link, image: './images/buddyguybot.png' };
              case 'Github-finder':
                return { ...link, image: './images/githubfinder.png' };
              case 'Bamazon':
                return { ...link, image: './images/bamazon.png' };
              case 'Constructor-Word-Guess':
                return { ...link, image: './images/wordguess.png' };
              case 'liri-node-app':
                return { ...link, image: './images/liri.png' };
              case 'what_happened_on':
                return { ...link, image: './images/whathappened.png' };
              case 'TipCalculator':
                return { ...link, image: './images/tip.png' };
              case 'TrainScheduler':
                return { ...link, image: './images/train.png' };
              case 'Giftastic':
                return { ...link, image: './images/giftastic.png' };
              case 'TriviaGame':
                return { ...link, image: './images/trivia.png' };
              case 'Word-Guess-Game':
                return { ...link, image: './images/guess.png' };
              case 'Domnoo':
                return { ...link, image: './images/domnoo.png' };
              case 'v-gamespot':
                return { ...link, image: './images/vgamespot.png' };
              default:
                return link;
            }
          });
      });
  }
};
</script>
